import { ENDPOINTS } from '@shared';
import { ApiService } from '@services';
import { Component, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { format } from 'date-fns';
import { MatTableDataSource } from '@angular/material/table';

@Component({
  selector: 'app-weekly-user-schedule',
  templateUrl: './weekly-user-schedule.component.html',
  styleUrls: ['./weekly-user-schedule.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class WeeklyUserScheduleComponent implements OnInit {

  users: any[];
  todayDate: Date;
  selectedDates: any[] = [];
  originalUserDates: any[] = [];
  selectedUser: any;
  userExistingRecord: any = null;
  dataSource: MatTableDataSource<any[]>;
  displayedColumns: string[] = ['date'];
  @ViewChild('calendar') calendar: any;

  constructor(
    private apiService: ApiService
  ) { }

  ngOnInit(): void {
    this.initializeUsers();
    this.todayDate = new Date();
  }

  onCalendarUpdate(e, calendar): void {
    const date = format(e, 'yyyy-MM-dd');
    if (this.originalUserDates.find(d => d === date)) {
      return;
    }

    if (!this.selectedDates.find(e => e === date)) {
      this.selectedDates.push(date);
    } else {
      const index = this.selectedDates.findIndex(e => e === date);
      this.selectedDates.splice(index, 1);
    }
    this.calendar.updateTodaysDate();

    this.updateDataSource();
  }

  onSelectUser(user): void {
    this.selectedUser = user;
    this.initializeUserDates(this.selectedUser);
  }

  isSelected(componentInstance): any {
    return (date) => {
      return Boolean(componentInstance.selectedDates?.find((e) => format(date, 'yyyy-MM-dd') === e)) 
      ? 'selected' 
      : null;
    }
  }

  saveSelectedDates(): void {
    const payload = {
      account: this.selectedUser,
      weeklyUserScheduleDates: this.convertSelectedDates(),
      id: this.userExistingRecord ? this.userExistingRecord.id : null
    }

    if (this.userExistingRecord) {
      this.apiService.update(ENDPOINTS.weeklyUserSchedule, payload).subscribe(() => {
        this.initializeUserDates(this.selectedUser);
      });
    } else {
      this.apiService.insert(ENDPOINTS.weeklyUserSchedule, { ...payload, weeklyUserScheduleDates: []}, true).subscribe((e) => {
        this.apiService.update(ENDPOINTS.weeklyUserSchedule, { ...payload, id: e.responseData.data.id}).subscribe(() => {
          this.initializeUserDates(this.selectedUser);
        });
      });
    }
  }

  convertDate(date): string {
    return format(new Date(date), 'MMM/dd/yyyy');
  }

  deleteDate(date): void {
    const indexOriginal = this.originalUserDates.findIndex((e) => e === date);
    if (indexOriginal !== -1) {
      const id = this.userExistingRecord.weeklyUserScheduleDates
        .find(e => format(new Date(e.scheduleDate), 'yyyy-MM-dd') === format(new Date(date), 'yyyy-MM-dd')).id;
      this.apiService.deleteById(ENDPOINTS.weeklyScheduledUsers, id).subscribe(() => {
        const index = this.userExistingRecord.weeklyUserScheduleDates.findIndex((e) => e.id === id);
        this.userExistingRecord.weeklyUserScheduleDates.splice(index, 1);
        this.deletionUpdate(date);
        this.originalUserDates.splice(indexOriginal, 1);
      });
    } else {
      this.deletionUpdate(date);
    }
  }

  deletionUpdate(date): void {
    const indexSelected = this.selectedDates.findIndex((e) => e === date);
    this.selectedDates.splice(indexSelected, 1);
    this.updateDataSource();
    this.calendar.updateTodaysDate();
  }

  private updateDataSource(): void {
    this.dataSource = new MatTableDataSource<any>(
      this.selectedDates.sort((a,b) => (new Date(b).valueOf()) - (new Date(a).valueOf()) ).map((date) => ({ date }))
    );
  }

  private convertSelectedDates(): any[] {
    return this.selectedDates.map((e) => {
        let originalDate = null;
        if (this.userExistingRecord) {
          originalDate = this.userExistingRecord.weeklyUserScheduleDates
          .find((c) => format(new Date(c.scheduleDate), 'yyyy-MM-dd') === format(new Date(e), 'yyyy-MM-dd') );
        }
        return originalDate ?? { scheduleDate: e };
    });
  }

  private initializeUsers(): void {
    this.apiService.findAll(`${ENDPOINTS.userAccountType}/ASC_USER`).subscribe(
      (res: any) => {
       this.users = res.responseData.data;
    });
  }

  private clearData(): void {
    this.selectedDates = [];
    this.originalUserDates = [];
    this.calendar.updateTodaysDate();
    this.updateDataSource();
  }

  private initializeUserDates(user): void {
    this.clearData();
    this.apiService.findAll(`${ ENDPOINTS.weeklyUserScheduleCustomSearch }?&accountId=${ user.id }`).subscribe((res) => {
      if (res.data.length) {
        res = res.data[0];
        this.originalUserDates = res.weeklyUserScheduleDates.map((e) => format(new Date(e.scheduleDate), 'yyyy-MM-dd'));
        this.selectedDates = [...this.originalUserDates];
        this.userExistingRecord = res;
        this.updateDataSource();
        this.calendar.updateTodaysDate();
      } else {
        this.userExistingRecord = null;
      }
    });
  }

}
